import React, { useState } from 'react';
import './../../theme/newsletters.scss';
import NewslettersList from './NewslettersList';
import Recipient from './Recipient';

const screens: any = [
  NewslettersList,
  Recipient
];

const Newsletters : React.FC = (props) => {
  const {
  } = props;

  const [content, setContent] = useState(0);
  const Component = screens[content];
  const menu = [
    "Newsletters",
    "Recipient Emails"
  ];

  return (
    <div className="newsletters" data-testid="newsletters">
      <div className='menu'>
        {menu.map(( name, index ) => (
          <h4
            key={index}
            className={`${index === content && 'menu-active'}`}
            onClick={() => setContent(index)}
          >
            {name}
          </h4>
        ))}
      </div>
      <div>
        <Component setContent={setContent} />
      </div>
    </div>
  );
}

export default Newsletters;
