import React, { useState } from 'react';
import '../../../theme/components/core/input.scss';

interface Input {
  onInput?: () => void
  type?: string
  id?: string
  accept?: string
  multiple?: boolean
  placeholder?: string
  required?: boolean
}

const Input : React.FC<Input> = (props) => {
  const {
    onInput,
    type,
    id,
    accept,
    placeholder,
    required
  } = props;

  return (
    <input
      onInput={onInput}
      className='input'
      type={type}
      id={id}
      accept={accept}
      multiple
      placeholder={placeholder}
      required={required}
      {... props}
    />
  );
}

export default Input;
