import React from "react";
import '../../../theme/components/core/listItem.scss'

interface ListItem {
  children?: React.ReactNode
};

const ListItem : React.FC<ListItem> = (props) => {
  const {
    children
  } = props;

  return (
    <div
      className="listItem"
      data-testid="listItem"
    >
      {children}
    </div>
  );
};

export default ListItem;
